import template from './sw-cms-el-zen-text-banner.html.twig';
import './sw-cms-el-zen-text-banner.scss';

const {Component, Mixin, Filter} = Shopware;

Component.register('sw-cms-el-zen-text-banner', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    computed: {
        displayModeClass() {
            if (this.element.config.displayMode.value === 'standard') {
                return null;
            }

            return `is--${this.element.config.displayMode.value}`;
        },

        elemStyles() {
            let styles = {};

            if (this.element.config.imageBorderRadius.value) {
                styles['border-radius'] = `${this.element.config.imageBorderRadiusTopLeft.value} ${this.element.config.imageBorderRadiusTopRight.value} ${this.element.config.imageBorderRadiusBottomRight.value} ${this.element.config.imageBorderRadiusBottomLeft.value}`;
            }

            styles['min-height'] = this.element.config.displayMode.value === 'cover' &&
            this.element.config.minHeight.value &&
            this.element.config.minHeight.value !== 0 ? this.element.config.minHeight.value : 'auto';
            styles['align-self'] = this.element.config.verticalAlign.value || 'flex-start';

            return styles;
        },

        imgStyles() {
            let styles = {};

            if (this.element.config.imageBorderRadius.value) {
                styles['border-radius'] = `${this.element.config.imageBorderRadiusTopLeft.value} ${this.element.config.imageBorderRadiusTopRight.value} ${this.element.config.imageBorderRadiusBottomRight.value} ${this.element.config.imageBorderRadiusBottomLeft.value}`;
            }

            if (this.element.config.displayMode.value === 'cover') {
                styles['object-position'] = `${this.element.config.horizontalImageAlign.value || 'center'} ${this.element.config.verticalImageAlign.value || 'center'}`;
            }

            return styles;
        },

        horizontalAlign() {
            return {
                'justify-content': this.element.config.horizontalAlign?.value || null,
            };
        },

        overlayStyles() {
            let styles = {};

            if (this.element.config.overlay.value) {
                styles['background-color'] = this.element.config.overlayColor.value ? this.element.config.overlayColor.value : 'transparent';
                styles['opacity'] = this.element.config.overlayOpacity.value ? this.element.config.overlayOpacity.value : '0%';
            }

            if (this.element.config.imageBorderRadius.value) {
                styles['border-radius'] = `${this.element.config.imageBorderRadiusTopLeft.value} ${this.element.config.imageBorderRadiusTopRight.value} ${this.element.config.imageBorderRadiusBottomRight.value} ${this.element.config.imageBorderRadiusBottomLeft.value}`;
            }

            return styles;
        },

        contentContainerStyles() {
            return {
                'align-items': this.element.config.verticalContentAlign.value || null,
                'justify-content': this.element.config.horizontalContentAlign.value || null
            };
        },

        contentStyles() {
            let styles = {};

            styles['background-color'] = this.element.config.backgroundColor.value || null;
            styles['-webkit-backdrop-filter'] = `blur(${this.element.config.backgroundBlur.value || null})`;
            styles['backdrop-filter'] = `blur(${this.element.config.backgroundBlur.value || null})`;

            if (this.element.config.contentMinWidth.value && this.element.config.contentMaxWidth.value) {
                styles.width = `clamp( ${this.element.config.contentMinWidth.value}, 50%, ${this.element.config.contentMaxWidth.value} )`;
            } else {
                styles.width = this.element.config.contentMinWidth.value || null;
                styles['max-width'] = this.element.config.contentMaxWidth.value || null;
            }

            if (this.element.config.contentPaddings.value) {
                styles.padding = `${this.element.config.contentPaddingTop.value} ${this.element.config.contentPaddingRight.value} ${this.element.config.contentPaddingBottom.value} ${this.element.config.contentPaddingLeft.value}`;
            }

            if (this.element.config.contentMargins.value) {
                styles.margin = `${this.element.config.contentMarginTop.value} ${this.element.config.contentMarginRight.value} ${this.element.config.contentMarginBottom.value} ${this.element.config.contentMarginLeft.value}`;
            }

            if (this.element.config.contentBorderRadius.value) {
                styles['border-radius'] = `${this.element.config.contentBorderRadiusTopLeft.value} ${this.element.config.contentBorderRadiusTopRight.value} ${this.element.config.contentBorderRadiusBottomRight.value} ${this.element.config.contentBorderRadiusBottomLeft.value}`;
            }

            return styles;
        },

        editorStyles() {
            let styles = {};

            styles.color = this.element.config.textColor.value || null;

            return styles;
        },

        textEditorClasses() {
            let classes = [];

            classes.push(this.element.config.headlineScale.value ? 'is--heading-lg' : '');
            classes.push(this.element.config.textScale.value ? 'is--text-lg' : '');

            return classes;
        },

        mediaUrl() {
            const staticFallBackImage = this.assetFilter('zenitplatformgravity/static/img/cms/preview_four_large.jpg');
            const elemData = this.element.data.media;
            const elemConfig = this.element.config.media;

            if (elemConfig.source === 'mapped') {
                const demoMedia = this.getDemoValue(elemConfig.value);

                if (demoMedia?.url) {
                    return demoMedia.url;
                }

                return staticFallBackImage;
            }

            if (elemConfig.source === 'default') {

                // ... on media change / delete, the elemConfig.value is empty and there is no way
                // to get the blocks default value again... so we set staticFallBackImage
                if (elemConfig.value) {
                    return this.assetFilter(elemConfig.value);
                } else {
                    return staticFallBackImage;
                }
            }

            if (elemData?.id) {
                return this.element.data.media.url;
            }

            if (elemData?.url) {
                return this.assetFilter(elemConfig.url);
            }

            return staticFallBackImage;
        },

        assetFilter() {
            return Filter.getByName('asset');
        },
    },

    watch: {
        cmsPageState: {
            deep: true,
            handler() {
                this.$forceUpdate();
            },
        },
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zen-text-banner');
            this.initElementData('zen-text-banner');
        },

        onBlurText(content) {
            this.emitChangesText(content);
        },

        onInputText(content) {
            this.emitChangesText(content);
        },

        emitChangesText(content) {
            if (content !== this.element.config.text.value) {
                this.element.config.text.value = content;
                this.$emit('element-update', this.element);
            }
        },
    },
});