import template from './sw-cms-el-zen-code.html.twig';
import './sw-cms-el-zen-code.scss';

const {Component} = Shopware;

Component.register('sw-cms-el-zen-code', {
    template,

    mixins: [
        'cms-element'
    ],

    computed: {
        contentHTMLPreview() {
            return this.truncate(this.element.config.contentHTML.value, this.element.config.contentHTMLTruncateLength.value || '510', '. . .');
        },

        contentCSSPreview() {
            return this.truncate(this.element.config.contentCSS.value, this.element.config.contentCSSTruncateLength.value || '510', '. . .');
        },

        contentJSPreview() {
            return this.truncate(this.element.config.contentJS.value, this.element.config.contentJSTruncateLength.value || '510', '. . .');
        },

        isEmpty() {
            return (!this.element.config.contentHTML.value && !this.element.config.contentCSS.value && !this.element.config.contentJS.value);
        }
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zen-code');
        },

        truncate(text, length, clamp) {
            clamp = clamp || '...';
            let tempElement = document.createElement('div');
            tempElement.innerHTML = text;
            let content = tempElement.innerHTML;
            return content.length > length ? content.slice(0, length) + clamp : content;
        }
    }
});
