import './component';
import './config';
import './preview';

import deDE from './snippet/de-DE.json';
import enGB from './snippet/en-GB.json';

Shopware.Locale.extend('de-DE', deDE);
Shopware.Locale.extend('en-GB', enGB);

Shopware.Service('cmsService').registerCmsElement({
    name: 'zen-image-slider',
    label: 'sw-cms.elements.zenImageSlider.label',
    component: 'sw-cms-el-zen-image-slider',
    configComponent: 'sw-cms-el-config-zen-image-slider',
    previewComponent: 'sw-cms-el-preview-zen-image-slider',
    defaultConfig: {
        sliderItems: {
            source: 'static',
            value: [],
            required: true,
            entity: {
                name: 'media',
            },
        },
        navigationArrows: {
            source: 'static',
            value: 'inside',
        },
        navigationDots: {
            source: 'static',
            value: null,
        },
        displayMode: {
            source: 'static',
            value: 'standard',
        },
        minHeight: {
            source: 'static',
            value: '300px',
        },
        horizontalAlign: {
            source: 'static',
            value: null,
        },
        verticalAlign: {
            source: 'static',
            value: null,
        },
        elementBorderRadius: {
            source: 'static',
            value: false
        },
        elementBorderRadiusTopLeft: {
            source: 'static',
            value: "6px"
        },
        elementBorderRadiusTopRight: {
            source: 'static',
            value: "6px"
        },
        elementBorderRadiusBottomRight: {
            source: 'static',
            value: "6px"
        },
        elementBorderRadiusBottomLeft: {
            source: 'static',
            value: "6px"
        },
        customClasses: {
            source: 'static',
            value: null,
        },
        autoplay: {
            source: 'static',
            value: false,
        },
        autoplayTimeout: {
            source: 'static',
            value: 5000,
        },
        autoplayHoverPause: {
            source: 'static',
            value: true,
        },
        speed: {
            source: 'static',
            value: 500,
        },
        mode: {
            source: 'static',
            value: 'carousel',
        },
        axis: {
            source: 'static',
            value: 'horizontal',
        },
        multipleItems: {
            source: 'static',
            value: false,
        },
        items: {
            source: 'static',
            value: 1,
        },
        itemsXL: {
            source: 'static',
            value: 0,
        },
        itemsLG: {
            source: 'static',
            value: 0,
        },
        itemsMD: {
            source: 'static',
            value: 0,
        },
        itemsSM: {
            source: 'static',
            value: 1,
        },
        itemsXS: {
            source: 'static',
            value: 1,
        },
        gutter: {
            source: 'static',
            value: 0,
        },
        loop: {
            source: 'static',
            value: false,
        },
        rewind: {
            source: 'static',
            value: true,
        }
    },
    enrich: function enrich(elem, data) {
        if (Object.keys(data).length < 1) {
            return;
        }

        let entityCount = 0;
        Object.keys(elem.config).forEach((configKey) => {
            const entity = elem.config[configKey].entity;

            if (!entity) {
                return;
            }

            const entityKey = `entity-${entity.name}-${entityCount}`;
            entityCount += 1;

            if (!data[entityKey]) {
                return;
            }

            elem.data[configKey] = [];
            elem.config[configKey].value.forEach((sliderItem) => {
                elem.data[configKey].push({
                    codeEditor: sliderItem.codeEditor,
                    newTab: sliderItem.newTab,
                    url: sliderItem.url,
                    media: data[entityKey].get(sliderItem.mediaId),
                    text: sliderItem.text,
                    headlineScale: sliderItem.headlineScale,
                    textScale: sliderItem.textScale,
                    color: sliderItem.color,
                    backgroundColor: sliderItem.backgroundColor,
                    backgroundBlur: sliderItem.backgroundBlur,
                    textBorderRadius: sliderItem.textBorderRadius,
                    textBorderRadiusTopLeft: sliderItem.textBorderRadiusTopLeft,
                    textBorderRadiusTopRight: sliderItem.textBorderRadiusTopRight,
                    textBorderRadiusBottomRight: sliderItem.textBorderRadiusBottomRight,
                    textBorderRadiusBottomLeft: sliderItem.textBorderRadiusBottomLeft,
                    textPaddings: sliderItem.textPaddings,
                    textPaddingTop: sliderItem.textPaddingTop,
                    textPaddingRight: sliderItem.textPaddingRight,
                    textPaddingBottom: sliderItem.textPaddingBottom,
                    textPaddingLeft: sliderItem.textPaddingLeft,
                    textMargins: sliderItem.textMargins,
                    textMarginTop: sliderItem.textMarginTop,
                    textMarginRight: sliderItem.textMarginRight,
                    textMarginBottom: sliderItem.textMarginBottom,
                    textMarginLeft: sliderItem.textMarginLeft,
                    verticalTextAlign: sliderItem.verticalTextAlign,
                    horizontalTextAlign: sliderItem.horizontalTextAlign,
                    textMinWidth: sliderItem.textMinWidth,
                    textMaxWidth: sliderItem.textMaxWidth,
                    horizontalImageAlign: sliderItem.horizontalImageAlign,
                    verticalImageAlign: sliderItem.verticalImageAlign,
                    overlay: sliderItem.overlay,
                    overlayColor: sliderItem.overlayColor,
                    overlayOpacity: sliderItem.overlayOpacity,
                    imageBorderRadius: sliderItem.imageBorderRadius,
                    imageBorderRadiusTopLeft: sliderItem.imageBorderRadiusTopLeft,
                    imageBorderRadiusTopRight: sliderItem.imageBorderRadiusTopRight,
                    imageBorderRadiusBottomLeft: sliderItem.imageBorderRadiusBottomLeft,
                    imageBorderRadiusBottomRight: sliderItem.imageBorderRadiusBottomRight,
                    animationIn: sliderItem.animationIn,
                    animation: sliderItem.animation,
                    customItemClasses: sliderItem.customItemClasses,
                });
            });
        });
    },
});
