import template from './sw-cms-el-zen-grid-placeholder.html.twig';
import './sw-cms-el-zen-grid-placeholder.scss';

const {Component, Mixin} = Shopware;

Component.register('sw-cms-el-zen-grid-placeholder', {
    template,

    inject: ['cmsService'],

    mixins: [
        Mixin.getByName('cms-element')
    ],

    created() {
        this.createdComponent();
    },

    data() {
        return {
            showElementSelection: false,
        };
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zen-grid-placeholder');
            this.initElementData('zen-grid-placeholder');
        },
    },
});