import template from './sw-cms-el-config-zen-teaser.html.twig';
import './sw-cms-el-config-zen-teaser.scss';

const {Component, Mixin} = Shopware;

Component.register('sw-cms-el-config-zen-teaser', {
    template,

    inject: ['repositoryFactory'],

    mixins: [
        Mixin.getByName('cms-element')
    ],

    data() {
        return {
            mediaModalIsOpen: false,
            initialFolderId: null,
        };
    },

    computed: {
        mediaRepository() {
            return this.repositoryFactory.create('media');
        },

        uploadTag() {
            return `cms-element-media-config-${this.element.id}`;
        },

        previewSource() {
            if (this.element.data && this.element.data.media && this.element.data.media.id) {
                return this.element.data.media;
            }

            return this.element.config.media.value;
        },

        onOutSelected() {
            const substring = '-out';
            return !!this.element.config.animationIn.value.includes(substring);
        },

        onInSelected() {
            const substring = '-in';
            return !!this.element.config.animationIn.value.includes(substring);
        },
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zen-teaser');
        },

        async onImageUpload({ targetId }) {
            const mediaEntity = await this.mediaRepository.get(targetId);

            this.element.config.media.value = mediaEntity.id;
            this.element.config.media.source = 'static';

            this.updateElementData(mediaEntity);

            this.$emit('element-update', this.element);
        },

        onImageRemove() {
            this.element.config.media.value = null;

            this.updateElementData();

            this.$emit('element-update', this.element);
        },

        onCloseModal() {
            this.mediaModalIsOpen = false;
        },

        onSelectionChanges(mediaEntity) {
            const media = mediaEntity[0];
            this.element.config.media.value = media.id;
            this.element.config.media.source = 'static';

            this.updateElementData(media);

            this.$emit('element-update', this.element);
        },

        updateElementData(media = null) {
            const mediaId = media === null ? null : media.id;
            if (!this.element.data) {
                this.$set(this.element, 'data', { mediaId, media });
            } else {
                this.$set(this.element.data, 'mediaId', mediaId);
                this.$set(this.element.data, 'media', media);
            }
        },

        onChangeAnimationInMode() {
            const substringIn = '-in';
            const substringOut = '-out';

            if (this.element.config.animationIn.value === 'none') {
                this.element.config.animationOut.value = 'none';
            }

            if (this.element.config.animationIn.value.includes(substringIn) && this.element.config.animationOut.value.includes(substringIn)) {
                this.element.config.animationOut.value = 'fade-out';
            }

            if (this.element.config.animationIn.value.includes(substringOut) && this.element.config.animationOut.value.includes(substringOut)) {
                this.element.config.animationOut.value = 'fade-in';
            }

            this.$emit('element-update', this.element);
        },

        onOpenMediaModal() {
            this.mediaModalIsOpen = true;
        },

        onChangeMinHeight(value) {
            this.element.config.minHeight.value = value === null ? '' : value;

            this.$emit('element-update', this.element);
        },

        onChangeDisplayMode(value) {
            if (value === 'cover') {
                this.element.config.verticalAlign.value = null;
                this.element.config.horizontalAlign.value = null;
            }

            this.$emit('element-update', this.element);
        },

        onChangeOverlayMode() {
            this.$emit('element-update', this.element);
        },

        onChangeTextBorderRadiusMode() {
            this.$emit('element-update', this.element);
        },

        onChangeImageBorderRadiusMode() {
            this.$emit('element-update', this.element);
        },

        onChangeTextPaddingsMode() {
            this.$emit('element-update', this.element);
        },

        onChangeTextMarginsMode() {
            this.$emit('element-update', this.element);
        },
    },
});