import template from './sw-cms-el-zen-breadcrumb.html.twig';
import './sw-cms-el-zen-breadcrumb.scss';

Shopware.Component.register('sw-cms-el-zen-breadcrumb', {
    template,

    mixins: [
        Shopware.Mixin.getByName('cms-element')
    ],

    created() {
        this.createdComponent();
    },

    computed: {
        horizontalAlignStyle() {
            if (!this.element.config.horizontalAlign.value) {
                return null;
            }

            return `display: flex; justify-content: ${this.element.config.horizontalAlign.value};`;
        }
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zen-breadcrumb');
        }
    }
});
