import template from './sw-cms-el-config-zen-image-slider.html.twig';
import './sw-cms-el-config-zen-image-slider.scss';

const { Component, Mixin } = Shopware;
const { moveItem, object: { cloneDeep } } = Shopware.Utils;
const Criteria = Shopware.Data.Criteria;

Component.register('sw-cms-el-config-zen-image-slider', {
    template,

    inject: ['repositoryFactory'],

    mixins: [
        Mixin.getByName('cms-element'),
    ],

    data() {
        return {
            mediaModalIsOpen: false,
            initialFolderId: null,
            entity: this.element,
            mediaItems: [],
        };
    },

    mediaItemDefaults: {
        text: null,
        headlineScale: false,
        textScale: false,
        color: '#ffffff',
        backgroundColor: 'transparent',
        backgroundBlur: '0px',
        textBorderRadius: false,
        textBorderRadiusTopLeft: '3px',
        textBorderRadiusTopRight: '3px',
        textBorderRadiusBottomRight: '3px',
        textBorderRadiusBottomLeft: '3px',
        textPaddings: false,
        textPaddingTop: '8px',
        textPaddingRight: '16px',
        textPaddingBottom: '8px',
        textPaddingLeft: '16px',
        textMargins: false,
        textMarginTop: '20px',
        textMarginRight: '10%',
        textMarginBottom: '20px',
        textMarginLeft: '10%',
        verticalTextAlign: 'center',
        horizontalTextAlign: 'flex-start',
        textMinWidth: null,
        textMaxWidth: '600px',
        horizontalImageAlign: 'center',
        verticalImageAlign: 'center',
        overlay: false,
        overlayColor: '#000000',
        overlayOpacity: '50%',
        imageBorderRadius: false,
        imageBorderRadiusTopLeft: '6px',
        imageBorderRadiusTopRight: '6px',
        imageBorderRadiusBottomRight: '6px',
        imageBorderRadiusBottomLeft: '6px',
        animationIn: 'none',
        animation: 'none',
        customItemClasses: null,
    },

    computed: {
        uploadTag() {
            return `cms-element-media-config-${this.element.id}`;
        },

        mediaRepository() {
            return this.repositoryFactory.create('media');
        },

        defaultFolderName() {
            return this.cmsPageState.pageEntityName;
        },

        items() {
            if (this.element.config && this.element.config.sliderItems && this.element.config.sliderItems.value) {
                return this.element.config.sliderItems.value;
            }

            return [];
        },
    },

    created() {
        this.createdComponent();
        this.initMultipleItems();
        this.onChangeItems();
    },

    methods: {
        onClickTab(index) {
            this.element.currentItem = index;
        },

        async createdComponent() {
            this.initElementConfig('zen-image-slider');

            if (this.element.config.sliderItems.source !== 'default' && this.element.config.sliderItems.value.length > 0) {
                const mediaIds = this.element.config.sliderItems.value.map((configElement) => {
                    return configElement.mediaId;
                });

                const criteria = new Criteria();
                criteria.setIds(mediaIds);

                const searchResult = await this.mediaRepository.search(criteria);
                this.mediaItems = mediaIds.map((mediaId) => {
                    return searchResult.get(mediaId);
                });
            }
        },

        onImageUpload(mediaItem) {
            const sliderItems = this.element.config.sliderItems;
            if (sliderItems.source === 'default') {
                sliderItems.value = [];
                sliderItems.source = 'static';
            }

            sliderItems.value.push({
                mediaUrl: mediaItem.url,
                mediaId: mediaItem.id,
                url: null,
                newTab: false,
                ...this.$options.mediaItemDefaults
            });

            this.mediaItems.push(mediaItem);

            this.updateMediaDataValue();
            this.emitUpdateEl();
        },

        onItemRemove(mediaItem, index) {
            const key = mediaItem.id;
            const { value } = this.element.config.sliderItems;

            this.element.config.sliderItems.value = value.filter(
                (item, i) => {
                    return (item.mediaId !== key || i !== index);
                },
            );

            this.mediaItems = this.mediaItems.filter(
                (item, i) => {
                    return (item.id !== key || i !== index);
                },
            );

            this.updateMediaDataValue();
            this.emitUpdateEl();
        },

        onCloseMediaModal() {
            this.mediaModalIsOpen = false;
        },

        onMediaSelectionChange(mediaItems) {
            const sliderItems = this.element.config.sliderItems;
            if (sliderItems.source === 'default') {
                sliderItems.value = [];
                sliderItems.source = 'static';
            }

            mediaItems.forEach((item) => {
                this.element.config.sliderItems.value.push({
                    mediaUrl: item.url,
                    mediaId: item.id,
                    url: null,
                    newTab: false,
                    ...this.$options.mediaItemDefaults
                });
            });

            this.mediaItems.push(...mediaItems);

            this.updateMediaDataValue();
            this.emitUpdateEl();
        },

        onItemSort(dragData, dropData) {
            moveItem(this.mediaItems, dragData.position, dropData.position);
            moveItem(this.element.config.sliderItems.value, dragData.position, dropData.position);

            this.updateMediaDataValue();
            this.emitUpdateEl();
        },

        updateMediaDataValue() {
            if (this.element.config.sliderItems.value) {
                const sliderItems = cloneDeep(this.element.config.sliderItems.value);

                sliderItems.forEach((sliderItem) => {
                    this.mediaItems.forEach((mediaItem) => {
                        if (sliderItem.mediaId === mediaItem.id) {
                            sliderItem.media = mediaItem;
                        }
                    });
                });

                if (!this.element.data) {
                    this.$set(this.element, 'data', { sliderItems });
                } else {
                    this.$set(this.element.data, 'sliderItems', sliderItems);
                }
            }
        },

        onOpenMediaModal() {
            this.mediaModalIsOpen = true;
        },

        onChangeMinHeight(value) {
            this.element.config.minHeight.value = value === null ? '' : value;

            this.$emit('element-update', this.element);
        },

        onChangeDisplayMode(value) {
            if (value === 'cover') {
                this.element.config.verticalAlign.value = null;
                this.element.config.horizontalAlign.value = null;
            }

            this.$emit('element-update', this.element);
        },

        onChangeEditorMode() {
            this.$emit('element-update', this.element);
        },

        onChangeOverlayMode() {
            this.$emit('element-update', this.element);
        },

        onChangeImageBorderRadiusMode() {
            this.$emit('element-update', this.element);
        },

        onChangeTextBorderRadiusMode() {
            this.$emit('element-update', this.element);
        },

        onChangeTextPaddingsMode() {
            this.$emit('element-update', this.element);
        },

        onChangeTextMarginsMode() {
            this.$emit('element-update', this.element);
        },

        onChangeAxisMode() {
            this.$emit('element-update', this.element);
        },

        onChangeAutoplayMode() {
            this.$emit('element-update', this.element);
        },

        onChangeElementBorderRadiusMode() {
            this.$emit('element-update', this.element);
        },

        initMultipleItems() {
            if (this.element.config.items.value > 1) {
                this.element.config.multipleItems.value = true;
            }
        },

        onChangeMultipleItems(val) {
            if (val === false) {
                this.element.config.items.value = 1;
                this.element.config.itemsXL.value = 1;
                this.element.config.itemsLG.value = 1;
                this.element.config.itemsMD.value = 1;
                this.element.config.itemsSM.value = 1;
                this.element.config.itemsXS.value = 1;
            }

            this.$emit('element-update', this.element);
        },

        onChangeItems() {
            // set defaults for already configured elements
            this.element.config.itemsXL.value === 0 && (this.element.config.itemsXL.value = this.element.config.items.value);
            this.element.config.itemsLG.value === 0 && (this.element.config.itemsLG.value = this.element.config.items.value);
            this.element.config.itemsMD.value === 0 && (this.element.config.itemsMD.value = this.element.config.items.value);

            this.$emit('element-update', this.element);
        },

        onTextBlur(event, content) {
            this.emitChanges(content);
        },

        onTextInput(event, content) {
            this.emitChanges(content);
        },

        emitChanges(content) {
            if (content !== this.element.config.sliderItems.value.text) {
                this.element.config.sliderItems.value.text = content;
                this.$emit('element-update', this.element);
            }
        },

        emitUpdateEl() {
            this.$emit('element-update', this.element);
        },
    },
});
