import template from './sw-cms-el-zen-features.html.twig';
import './sw-cms-el-zen-features.scss';
import {iconSVG} from "../icon";

const {Component} = Shopware;

Component.register('sw-cms-el-zen-features', {
    template,

    mixins: [
        'cms-element'
    ],

    computed: {
        elemStyles() {
            let styles = {};

            styles['display'] = 'flex';

            if (this.element.config.appearance.value === 'iconsTop') {
                styles['flex-direction'] = 'column';

                if (this.element.config.alignment.value === 'start') {
                    styles['align-items'] = 'flex-start';
                }

                if (this.element.config.alignment.value === 'center') {
                    styles['align-items'] = 'center';
                }

                if (this.element.config.alignment.value === 'end') {
                    styles['align-items'] = 'flex-end';
                }
            } else {
                if (this.element.config.alignment.value === 'start') {
                    styles['justify-content'] = 'flex-start';
                }

                if (this.element.config.alignment.value === 'center') {
                    styles['justify-content'] = 'center';
                }

                if (this.element.config.alignment.value === 'end') {
                    styles['justify-content'] = 'flex-end';
                }
            }

            return styles;
        },

        iconStyles() {
            let styles = {};

            styles.color = this.element.config.iconColor.value || null;
            styles.width = this.element.config.iconSize.value ? this.element.config.iconSize.value : '22px';
            styles.height = this.element.config.iconSize.value ? this.element.config.iconSize.value : '22px';

            if (this.element.config.appearance.value === 'iconsTop') {
                styles['display'] = 'flex';
                styles['margin-bottom'] = parseInt(this.element.config.iconSize.value) / 2 + 'px';
            }

            if (this.element.config.appearance.value === 'iconsLeft') {
                styles['margin-right'] = parseInt(this.element.config.iconSize.value) / 2 + 'px';
            }

            return styles;
        },

        textStyles() {
            let styles = {};

            styles['text-align'] = this.element.config.alignment.value ? this.element.config.alignment.value : 'left';
            styles['font-size'] = this.element.config.fontSize.value ? this.element.config.fontSize.value : '16px';
            styles['font-weight'] = this.element.config.fontWeight.value ? this.element.config.fontWeight.value : '800';
            styles.color = this.element.config.textColor.value || null;

            return styles;
        },

        features() {
            let text = [
                this.element.config.feature1.value ? this.element.config.feature1.value : null,
                this.element.config.feature2.value ? this.element.config.feature2.value : null,
                this.element.config.feature3.value ? this.element.config.feature3.value : null,
                this.element.config.feature4.value ? this.element.config.feature4.value : null,
            ];

            return text.filter(n => n);
        },

        icons() {
            let icons = [
                (this.element.config.feature1.value && this.element.config.feature1Icon.value) ? this.element.config.feature1Icon.value : null,
                (this.element.config.feature2.value && this.element.config.feature2Icon.value) ? this.element.config.feature2Icon.value : null,
                (this.element.config.feature3.value && this.element.config.feature3Icon.value) ? this.element.config.feature3Icon.value : null,
                (this.element.config.feature4.value && this.element.config.feature4Icon.value) ? this.element.config.feature4Icon.value : null,
            ];

            return icons.filter(n => n);
        }
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zen-features');
        },
        getIconSVG(icon) {
            return iconSVG(icon);
        }
    }
});