const {Mixin} = Shopware;

Shopware.Component.override('sw-extension-card-base', {

    mixins: [
        Mixin.getByName('notification'),
    ],

    created() {
        this.updateNotification();
    },

    methods: {
        updateNotification() {
            if (this.extension.name === 'zenitPlatformGravity') {
                const themeUpdateVersion = '2.13.2';
                const updateNotificationPriority = 'warning';
                const updateNotificationStorageKey = 'zenit-update-guide-notification'
                const updateNotificationStorageValue = localStorage.getItem(updateNotificationStorageKey);
                const updateNotificationPriorityText = this.$tc(`zen-theme-gravity.extension.general.updateNotificationPriority.${updateNotificationPriority}`);

                if (this.extension.version === themeUpdateVersion) {
                    if (updateNotificationStorageValue !== themeUpdateVersion) {
                        this.createNotificationInfo({
                            title: this.$tc('zen-theme-gravity.extension.general.updateNotificationTitle', 0, {updatePriority: updateNotificationPriorityText}),
                            message: this.$tc('zen-theme-gravity.extension.general.updateNotificationMessage'),
                            variant: updateNotificationPriority,
                            system: true,
                            autoClose: false,
                        });

                        localStorage.setItem(updateNotificationStorageKey, themeUpdateVersion)
                    }
                }
            }
        }
    },
});
