import template from './sw-cms-block-zen-layout-simple-teaser.html.twig';
import './sw-cms-block-zen-layout-simple-teaser.scss';

const { Component } = Shopware;

Component.register('sw-cms-block-zen-layout-simple-teaser', {
    template,

    created() {
        this.$parent.block.isZenGravityBlock = true;

        if (!this.$parent.block.customFields) {
            this.$set(this.$parent.block, 'customFields', {});
        }
    },

    computed: {
        style() {
            if (this.$parent.block.customFields && this.$parent.block.customFields.zenit_grid_gap) {
                return {gridGap: this.$parent.block.customFields.zenit_grid_gap}
            }
        }
    }
});
