import template from './sw-cms-el-zen-teaser.html.twig';
import './sw-cms-el-zen-teaser.scss';

const {Component, Mixin, Filter} = Shopware;

Component.register('sw-cms-el-zen-teaser', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    computed: {
        displayModeClass() {
            if (this.element.config.displayMode.value === 'standard') {
                return null;
            }

            return `is--${this.element.config.displayMode.value}`;
        },

        elemStyles() {
            let styles = {};

            if (this.element.config.imageBorderRadius.value) {
                styles['border-radius'] = `${this.element.config.imageBorderRadiusTopLeft.value} ${this.element.config.imageBorderRadiusTopRight.value} ${this.element.config.imageBorderRadiusBottomRight.value} ${this.element.config.imageBorderRadiusBottomLeft.value}`;
            }

            styles['min-height'] = this.element.config.displayMode.value === 'cover' &&
            this.element.config.minHeight.value &&
            this.element.config.minHeight.value !== 0 ? this.element.config.minHeight.value : 'auto';
            styles['align-self'] = this.element.config.verticalAlign.value || 'flex-start';

            return styles;
        },

        imgStyles() {
            let styles = {};

            if (this.element.config.imageBorderRadius.value) {
                styles['border-radius'] = `${this.element.config.imageBorderRadiusTopLeft.value} ${this.element.config.imageBorderRadiusTopRight.value} ${this.element.config.imageBorderRadiusBottomRight.value} ${this.element.config.imageBorderRadiusBottomLeft.value}`;
            }

            if (this.element.config.displayMode.value === 'cover') {
                styles['object-position'] = `${this.element.config.horizontalImageAlign.value || 'center'} ${this.element.config.verticalImageAlign.value || 'center'}`;
            }

            return styles;
        },

        horizontalAlign() {
            return {
                'justify-content': this.element.config.horizontalAlign?.value || null,
            };
        },

        overlayStyles() {
            let styles = {};

            if (this.element.config.overlay.value) {
                styles['background-color'] = this.element.config.overlayColor.value ? this.element.config.overlayColor.value : 'transparent';
                styles['opacity'] = this.element.config.overlayOpacity.value ? this.element.config.overlayOpacity.value : '0%';
            }

            if (this.element.config.imageBorderRadius.value) {
                styles['border-radius'] = `${this.element.config.imageBorderRadiusTopLeft.value} ${this.element.config.imageBorderRadiusTopRight.value} ${this.element.config.imageBorderRadiusBottomRight.value} ${this.element.config.imageBorderRadiusBottomLeft.value}`;
            }

            return styles;
        },

        textContainerStyles() {
            return {
                'align-items': this.element.config.verticalTextAlign.value || null,
                'justify-content': this.element.config.horizontalTextAlign.value || null
            };
        },

        textStyles() {
            let styles = {};

            styles['--zen-font-size-max'] = this.element.config.fontSize.value ? this.element.config.fontSize.value : '16px';
            styles['text-align'] = this.element.config.textAlign.value ? this.element.config.textAlign.value : 'center';
            styles['font-weight'] = this.element.config.fontWeight.value ? this.element.config.fontWeight.value : '400';
            styles.color = this.element.config.color.value || null;
            styles['background-color'] = this.element.config.backgroundColor.value || null;
            styles['-webkit-backdrop-filter'] = `blur(${this.element.config.backgroundBlur.value || null})`;
            styles['backdrop-filter'] = `blur(${this.element.config.backgroundBlur.value || null})`;

            if (this.element.config.textMinWidth.value && this.element.config.textMaxWidth.value) {
                styles.width = `clamp( ${this.element.config.textMinWidth.value}, 50%, ${this.element.config.textMaxWidth.value} )`;
            } else {
                styles.width = this.element.config.textMinWidth.value || null;
                styles['max-width'] = this.element.config.textMaxWidth.value || null;
            }

            if (this.element.config.textPaddings.value) {
                styles.padding = `${this.element.config.textPaddingTop.value} ${this.element.config.textPaddingRight.value} ${this.element.config.textPaddingBottom.value} ${this.element.config.textPaddingLeft.value}`;
            }

            if (this.element.config.textMargins.value) {
                styles.margin = `${this.element.config.textMarginTop.value} ${this.element.config.textMarginRight.value} ${this.element.config.textMarginBottom.value} ${this.element.config.textMarginLeft.value}`;
            }

            if (this.element.config.textBorderRadius.value) {
                styles['border-radius'] = `${this.element.config.textBorderRadiusTopLeft.value} ${this.element.config.textBorderRadiusTopRight.value} ${this.element.config.textBorderRadiusBottomRight.value} ${this.element.config.textBorderRadiusBottomLeft.value}`;
            }

            return styles;
        },

        mediaUrl() {
            const staticFallBackImage = this.assetFilter('zenitplatformgravity/static/img/cms/preview_one_large.jpg');
            const elemData = this.element.data.media;
            const elemConfig = this.element.config.media;

            if (elemConfig.source === 'mapped') {
                const demoMedia = this.getDemoValue(elemConfig.value);

                if (demoMedia?.url) {
                    return demoMedia.url;
                }

                return staticFallBackImage;
            }

            if (elemConfig.source === 'default') {

                // ... on media change / delete, the elemConfig.value is empty and there is no way
                // to get the blocks default value again... so we set staticFallBackImage
                if (elemConfig.value) {
                    return this.assetFilter(elemConfig.value);
                } else {
                    return staticFallBackImage;
                }
            }

            if (elemData?.id) {
                return this.element.data.media.url;
            }

            if (elemData?.url) {
                return this.assetFilter(elemConfig.url);
            }

            return staticFallBackImage;
        },

        assetFilter() {
            return Filter.getByName('asset');
        },
    },

    watch: {
        cmsPageState: {
            deep: true,
            handler() {
                this.$forceUpdate();
            },
        },
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zen-teaser');
            this.initElementData('zen-teaser');
        },
    },
});