import template from './sw-cms-el-zen-divider.html.twig';
import './sw-cms-el-zen-divider.scss';

const {Component} = Shopware;

Component.register('sw-cms-el-zen-divider', {
    template,

    mixins: [
        'cms-element'
    ],

    computed: {
        elemStyles() {
            let styles = {};

            styles['align-items'] = this.element.config.verticalAlign.value || null;
            styles['justify-content'] = this.element.config.horizontalAlign.value || null;

            return styles;
        },

        wrapperStyles() {
            let styles = {};

            styles['width'] = this.element.config.dividerWidth.value ? this.element.config.dividerWidth.value : '100%';

            return styles;
        },

        dividerStyles() {
            let styles = {};

            styles['border-top-style'] = this.element.config.dividerStyle.value ? this.element.config.dividerStyle.value : 'solid';
            styles['border-top-color'] = this.element.config.dividerColor.value ? this.element.config.dividerColor.value : '#333333';
            styles['border-top-width'] = this.element.config.dividerHeight.value ? this.element.config.dividerHeight.value : '1px';

            return styles;
        },

        hasText() {
            return this.element?.config?.text?.value;
        },

        showLeftDivider() {
            return this.element?.config?.textAlign?.value === 'right' || this.element?.config?.textAlign?.value === 'center';
        },

        showRightDivider() {
            return this.element?.config?.textAlign?.value === 'left' || this.element?.config?.textAlign?.value === 'center';
        },

        textStyles() {
            let styles = {};

            styles.color = this.element.config.color.value || null;
            styles['text-align'] = this.element.config.textAlign.value ? this.element.config.textAlign.value : 'center';
            styles['font-size'] = this.element.config.fontSize.value ? this.element.config.fontSize.value : '16px';
            styles['font-weight'] = this.element.config.fontWeight.value ? this.element.config.fontWeight.value : '400';

            if (this.element?.config?.textAlign?.value === 'left') {
                styles['margin-left'] = '0';
            }

            if (this.element?.config?.textAlign?.value === 'right') {
                styles['margin-right'] = '0';
            }

            return styles;
        },
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zen-divider');
        }
    }
});
