import template from './sw-cms-el-config-zen-code.html.twig';
import './sw-cms-el-config-zen-code.scss';

const { Component, Mixin } = Shopware;

Component.register('sw-cms-el-config-zen-code', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zen-code');
        }
    }
});
