import template from './sw-theme-manager-detail.html.twig';
import './sw-theme-manager-detail.scss';
import composerJson from '../../../../../../../../../../composer.json'

const {Component} = Shopware;

Component.override('sw-theme-manager-detail', {
    template,

    data() {
        return {
            plugin: null,
            pluginJsonGravity: composerJson,
            structuredThemeFields: {}
        }
    },

    computed: {
        isThemeGravity() {
            const pluginClass = this.pluginJsonGravity['extra']['shopware-plugin-class'];
            const pluginName = pluginClass.slice(pluginClass.lastIndexOf("\\") + 1);

            /* Child themes has no parentTheme, Duplicates may have */
            if (this.parentTheme && !!this.parentTheme.technicalName) {
                return this.parentTheme.technicalName === pluginName;
            }

            if (this.theme && !!this.theme.technicalName) {
                return this.theme.technicalName.includes(pluginName);
            }

            return false;
        },

        detailClasses: function () {
            const classes = [];

            classes.push('zenit');
            classes.push('gravity');

            return classes;
        },

        hasContent() {
            return Object.values(this.structuredThemeFields).length > 0 && !this.isLoading;
        },
    },

    watch: {
        isThemeGravity() {
            this.setPageClassesGravity();
        },

        hasContent() {
            if (this.isThemeGravity) {
                this.replaceText();
            }
        }
    },

    mounted() {
        this.setPageClassesGravity();
    },

    methods: {
        setPageClassesGravity: function() {
            if (this.$el) {
                this.detailClasses.forEach((cssClass) => {
                    if (this.isThemeGravity) {
                        this.$el.classList.add(cssClass);
                        return;
                    }
                    this.$el.classList.remove(cssClass)
                });
            }
        },

        replaceText() {
            for (const tab of Object.values(this.structuredThemeFields.tabs)) {
                for (const block of Object.values(tab.blocks)) {
                    for (const section of Object.values(block.sections)) {
                        const parts = section.label.split(' | ');
                        section.label = parts[0];
                        section.sublabel = parts[1];
                    }
                }
            }
        }
    },
});
