import './component';
import './preview';

import deDE from './snippet/de-DE.json';
import enGB from './snippet/en-GB.json';

Shopware.Locale.extend('de-DE', deDE);
Shopware.Locale.extend('en-GB', enGB);

Shopware.Service('cmsService').registerCmsBlock({
    name: 'zen-search-banner',
    label: 'sw-cms.blocks.zenSearchBanner.label',
    category: 'zen-elements',
    component: 'sw-cms-block-zen-search-banner',
    previewComponent: 'sw-cms-preview-zen-search-banner',
    defaultConfig: {
        marginBottom: '20px',
        marginTop: '20px',
        marginLeft: 'auto',
        marginRight: 'auto',
        sizingMode: 'boxed'
    },
    slots: {
        content: {
            type: 'zen-search-banner',
            default: {
                data: {
                    media: {
                        value: 'zenitplatformgravity/static/img/cms/preview_three_large.jpg',
                        source: 'default',
                    }
                }
            }
        }
    }
});