import template from './zen-theme-alert-info.html.twig';

const { Component, Mixin } = Shopware;
const registry = Component.getComponentRegistry();

if (!registry.has('zen-theme-alert-info')) {
    Component.register('zen-theme-alert-info', {
        template,

        props: {
            variant: {
                type: String,
                required: false,
                default: 'info'
            },
            appearance: {
                type: String,
                default: 'default',
            },
        },

        mixins: [
            Mixin.getByName('sw-form-field')
        ]
    });
}
