import template from './sw-cms-el-zen-image-slider.html.twig';
import './sw-cms-el-zen-image-slider.scss';

const { Component, Mixin, Filter } = Shopware;

Component.register('sw-cms-el-zen-image-slider', {
    template,

    inject: ['feature'],

    mixins: [
        Mixin.getByName('cms-element'),
    ],

    props: {
        activeMedia: {
            type: [Object, null],
            required: false,
            default: null,
        },
    },

    data() {
        return {
            columnCount: 7,
            columnWidth: 90,
            sliderPos: 0,
            imgPath: '/zenitplatformgravity/static/img/cms/preview_two_large.jpg',
            imgSrc: '',
            imgText: '',
            currentItem: 0,
        };
    },

    computed: {
        gridAutoRows() {
            return `grid-auto-rows: ${this.columnWidth}`;
        },

        uploadTag() {
            return `cms-element-media-config-${this.element.id}`;
        },

        sliderItems() {
            if (this.element?.config?.sliderItems?.source === 'mapped') {
                return this.getDemoValue(this.element.config.sliderItems.value) || [];
            }

            if (this.element.data && this.element.data.sliderItems && this.element.data.sliderItems.length > 0) {
                return this.element.data.sliderItems;
            }

            return [];
        },

        displayModeClass() {
            if (this.element.config.displayMode.value === 'standard') {
                return null;
            }

            return `is--${this.element.config.displayMode.value}`;
        },

        outsideNavArrows() {
            if (this.element.config.navigationArrows.value === 'outside') {
                return 'has--outside-arrows';
            }

            return null;
        },

        outsideNavDots() {
            if (this.element.config.navigationDots.value === 'outside') {
                return 'has--outside-dots';
            }

            return null;
        },

        navDotsClass() {
            if (this.element.config.navigationDots.value) {
                return `is--dot-${this.element.config.navigationDots.value}`;
            }

            return null;
        },

        navArrowsClass() {
            if (this.element.config.navigationArrows.value) {
                return `is--nav-${this.element.config.navigationArrows.value}`;
            }

            return null;
        },

        assetFilter() {
            return Filter.getByName('asset');
        },

        elemStyles() {
            let styles = {};

            if (this.element.config.elementBorderRadius.value) {
                styles['border-radius'] = `${this.element.config.elementBorderRadiusTopLeft.value} ${this.element.config.elementBorderRadiusTopRight.value} ${this.element.config.elementBorderRadiusBottomRight.value} ${this.element.config.elementBorderRadiusBottomLeft.value}`;
            }

            styles['align-self'] = this.element.config.verticalAlign.value || 'flex-start';

            return styles;
        },

        minHeightStyles() {
            let styles = {};

            styles['min-height'] = this.element.config.displayMode.value === 'cover' &&
            this.element.config.minHeight.value &&
            this.element.config.minHeight.value !== 0 ? this.element.config.minHeight.value : 'auto';

            return styles;
        },

        overlayStyles() {
            let styles = {};
            let overlayStyles = {};
            let imageBorderRadiusStyles = {};

            if (this.element.config.sliderItems.value) {
                for (let i = 0; i < this.element.config.sliderItems.value.length; i++) {
                    if (this.element.config.sliderItems.value[i].overlay) {
                        overlayStyles[i] = {
                            'background-color': this.element.config.sliderItems.value[i].overlayColor ? this.element.config.sliderItems.value[i].overlayColor : 'transparent',
                            'opacity': this.element.config.sliderItems.value[i].overlayOpacity ? this.element.config.sliderItems.value[i].overlayOpacity : '0%'
                        }
                        styles[i] = {...styles[i], ...overlayStyles[i]};
                    }

                    if (this.element.config.sliderItems.value[i].imageBorderRadius) {
                        imageBorderRadiusStyles[i] = {
                            'border-radius': `${this.element.config.sliderItems.value[i].imageBorderRadiusTopLeft} ${this.element.config.sliderItems.value[i].imageBorderRadiusTopRight} ${this.element.config.sliderItems.value[i].imageBorderRadiusBottomRight} ${this.element.config.sliderItems.value[i].imageBorderRadiusBottomLeft}`
                        }
                        styles[i] = {...styles[i], ...imageBorderRadiusStyles[i]};
                    }
                }
            }

            return styles;
        },

        imageStyles() {
            let styles = {};
            let alignmentStyles = {};
            let imageBorderRadiusStyles = {};

            if (this.element.config.sliderItems.value) {
                for (let i = 0; i < this.element.config.sliderItems.value.length; i++) {
                    if (this.element.config.displayMode.value === 'cover') {
                        alignmentStyles[i] = {
                            'object-position': `${this.element.config.sliderItems.value[i].horizontalImageAlign || 'center'} ${this.element.config.sliderItems.value[i].verticalImageAlign || 'center'}`
                        }
                        styles[i] = {...styles[i], ...alignmentStyles[i]};
                    }

                    if (this.element.config.sliderItems.value[i].imageBorderRadius) {
                        imageBorderRadiusStyles[i] = {
                            'border-radius': `${this.element.config.sliderItems.value[i].imageBorderRadiusTopLeft} ${this.element.config.sliderItems.value[i].imageBorderRadiusTopRight} ${this.element.config.sliderItems.value[i].imageBorderRadiusBottomRight} ${this.element.config.sliderItems.value[i].imageBorderRadiusBottomLeft}`
                        }
                        styles[i] = {...styles[i], ...imageBorderRadiusStyles[i]};
                    }
                }
            }

            return styles;
        },

        contentContainerStyles() {
            let styles = {};

            if (this.element.config.sliderItems.value) {
                for (let i = 0; i < this.element.config.sliderItems.value.length; i++) {
                    styles[i] = {
                        'align-items': this.element.config.sliderItems.value[i].verticalTextAlign || null,
                        'justify-content': this.element.config.sliderItems.value[i].horizontalTextAlign || null
                    }
                }
            }

            return styles;
        },

        editorClasses() {
            let classes = {};

            for (let i = 0; i < this.element.config.sliderItems.value.length; i++) {
                classes[i] = [];
                if (this.element.config.sliderItems.value[i].headlineScale) {
                    classes[i].push('is--heading-lg');
                }
                if (this.element.config.sliderItems.value[i].textScale) {
                    classes[i].push('is--text-lg');
                }
            }

            return classes;
        },


        textStyles() {
            let styles = {};
            let widthStyles = {};
            let paddingStyles = {};
            let textMarginsStyles = {};
            let textBorderRadiusStyles = {};

            if (this.element.config.sliderItems.value) {
                for (let i = 0; i < this.element.config.sliderItems.value.length; i++) {
                    styles[i] = {
                        'background-color': this.element.config.sliderItems.value[i].backgroundColor || null,
                        '-webkit-backdrop-filter': `blur(${this.element.config.sliderItems.value[i].backgroundBlur || null})`,
                        'backdrop-filter': `blur(${this.element.config.sliderItems.value[i].backgroundBlur || null})`,
                    }

                    if (this.element.config.sliderItems.value[i].textMinWidth && this.element.config.sliderItems.value[i].textMaxWidth) {
                        widthStyles[i] = {
                            width: `clamp( ${this.element.config.sliderItems.value[i].textMinWidth}, 50%, ${this.element.config.sliderItems.value[i].textMaxWidth} )`
                        }
                        styles[i] = {...styles[i], ...widthStyles[i]};
                    } else {
                        widthStyles[i] = {
                            width: this.element.config.sliderItems.value[i].textMinWidth || null,
                            'max-width': this.element.config.sliderItems.value[i].textMaxWidth || null
                        }
                        styles[i] = {...styles[i], ...widthStyles[i]};
                    }

                    if (this.element.config.sliderItems.value[i].textPaddings) {
                        paddingStyles[i] = {
                            padding: `${this.element.config.sliderItems.value[i].textPaddingTop} ${this.element.config.sliderItems.value[i].textPaddingRight} ${this.element.config.sliderItems.value[i].textPaddingBottom} ${this.element.config.sliderItems.value[i].textPaddingLeft}`
                        }
                        styles[i] = {...styles[i], ...paddingStyles[i]};
                    }

                    if (this.element.config.sliderItems.value[i].textMargins) {
                        textMarginsStyles[i] = {
                            margin: `${this.element.config.sliderItems.value[i].textMarginTop} ${this.element.config.sliderItems.value[i].textMarginRight} ${this.element.config.sliderItems.value[i].textMarginBottom} ${this.element.config.sliderItems.value[i].textMarginLeft}`
                        }
                        styles[i] = {...styles[i], ...textMarginsStyles[i]};
                    }

                    if (this.element.config.sliderItems.value[i].textBorderRadius) {
                        textBorderRadiusStyles[i] = {
                            'border-radius': `${this.element.config.sliderItems.value[i].textBorderRadiusTopLeft} ${this.element.config.sliderItems.value[i].textBorderRadiusTopRight} ${this.element.config.sliderItems.value[i].textBorderRadiusBottomRight} ${this.element.config.sliderItems.value[i].textBorderRadiusBottomLeft}`
                        }
                        styles[i] = {...styles[i], ...textBorderRadiusStyles[i]};
                    }
                }
            }

            return styles;
        },

        placeholderStyles() {
            let styles = {};

            if (this.element.config.sliderItems.value) {
                for (let i = 0; i < this.element.config.sliderItems.value.length; i++) {
                    styles[i] = {
                        'backgroundColor': this.element.config.sliderItems.value[i].color || null,
                        '-webkit-backdrop-filter': `blur(${this.element.config.sliderItems.value[i].backgroundBlur || null})`,
                        'backdrop-filter': `blur(${this.element.config.sliderItems.value[i].backgroundBlur || null})`,
                    }
                }
            }

            return styles;
        },

        editorStyles() {
            let styles = {};

            if (this.element.config.sliderItems.value) {
                for (let i = 0; i < this.element.config.sliderItems.value.length; i++) {
                    styles[i] = {
                        'color': this.element.config.sliderItems.value[i].color || null,
                    }
                }
            }

            return styles;
        },

        textValue() {
            let text = {};

            if (this.element.config.sliderItems.value) {
                for (let i = 0; i < this.element.config.sliderItems.value.length; i++) {
                    text[i] = this.element.config.sliderItems.value[i].text || null;
                }
            }

            return text;
        },
    },

    watch: {
        sliderItems: {
            handler() {
                if (this.sliderItems && this.sliderItems.length > 0) {
                    this.setSliderItem(this.sliderItems[0], 0);
                    this.$emit('active-image-change', this.sliderItems[0].media);
                } else {
                    this.imgSrc = this.assetFilter(this.imgPath);
                }
            },
            deep: true,
        },

        'element.currentItem': {
            handler() {
                this.setSliderItem(this.sliderItems[this.element.currentItem], this.element.currentItem);
            }
        },

        activeMedia() {
            this.sliderPos = this.activeMedia.sliderIndex;
            this.imgSrc = this.activeMedia.url;
        },
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zen-image-slider');
            this.initElementData('zen-image-slider');

            if (this.sliderItems && this.sliderItems.length > 0) {
                this.imgSrc = this.sliderItems[0].media.url;
                this.$emit('active-image-change', this.sliderItems[this.sliderPos].media);
            } else {
                this.imgSrc = this.assetFilter(this.imgPath);
            }
        },

        setSliderItem(mediaItem, index) {
            this.imgSrc = mediaItem.media.url;

            this.sliderPos = index;
            this.$emit('active-image-change', mediaItem.media, index);
        },

        activeButtonClass(url) {
            return {
                'is--active': this.imgSrc === url,
            };
        },

        setSliderArrowItem(direction = 1) {
            if (this.sliderItems.length < 2) {
                return;
            }

            this.sliderPos += direction;

            if (this.sliderPos < 0) {
                this.sliderPos = this.sliderItems.length - 1;
            }

            if (this.sliderPos > this.sliderItems.length - 1) {
                this.sliderPos = 0;
            }

            this.imgSrc = this.sliderItems[this.sliderPos].media.url;
            this.$emit('active-image-change', this.sliderItems[this.sliderPos].media, this.sliderPos);
        },

        onTextBlur(content, index) {
            this.emitChanges(content, index);
        },

        onTextInput(content, index) {
            this.emitChanges(content, index);
        },

        emitChanges(content, index) {
            for (let i = 0; i < this.element.config.sliderItems.value.length; i++) {
                if (content !== this.element.config.sliderItems.value[index].text) {
                    this.element.config.sliderItems.value[index].text = content;
                    this.$emit('element-update', this.element);
                }
            }
        },
    },
});
