import template from './sw-cms-el-config-zen-breadcrumb.html.twig';

const {Component} = Shopware;

Component.register('sw-cms-el-config-zen-breadcrumb', {
    template,

    mixins: [
        Shopware.Mixin.getByName('cms-element')
    ],

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zen-breadcrumb');
        }
    }
});
