import template from './sw-cms-block-zen-gallery-heading-description-buybox.html.twig';
import './sw-cms-block-zen-gallery-heading-description-buybox.scss';

const { Component, State } = Shopware;

Component.register('sw-cms-block-zen-gallery-heading-description-buybox', {
    template,

    computed: {
        currentDeviceView() {
            return State.get('cmsPageState').currentCmsDeviceView;
        },

        currentDeviceViewClass() {
            if (this.currentDeviceView) {
                return `is--${this.currentDeviceView}`;
            }

            return null;
        },
    },
});
