import './component';
import './config';
import './preview';

import deDE from './snippet/de-DE.json';
import enGB from './snippet/en-GB.json';

Shopware.Locale.extend('de-DE', deDE);
Shopware.Locale.extend('en-GB', enGB);

Shopware.Service('cmsService').registerCmsElement({
    name: 'zen-breadcrumb',
    label: 'sw-cms.elements.zenBreadcrumb.label',
    component: 'sw-cms-el-zen-breadcrumb',
    configComponent: 'sw-cms-el-config-zen-breadcrumb',
    previewComponent: 'sw-cms-el-preview-zen-breadcrumb',
    defaultConfig: {
        horizontalAlign: {
            source: 'static',
            value: 'flex-start'
        }
    }
});

